@import "mixins";
@include keyframes(show-bg-form) {
  0% {
    opacity: 0; }
  70% {
    opacity: .5; }
  100% {
    opacity: 1; } }
@include keyframes(show-bg-form-rev) {
  0% {
    opacity: 1; }
  70% {
    opacity: .5; }
  100% {
    opacity: 0; } }
@include keyframes(show-callback) {
  0% {
    @include transform(scale(0)); }
  70% {
    @include transform(scale(.5)); }
  100% {
    @include transform(scale(1)); } }
@include keyframes(show-callback-rev) {
  0% {
    @include transform(scale(1)); }
  70% {
    @include transform(scale(.5)); }
  100% {
    @include transform(scale(0)); } }
@include keyframes(preloader-heart) {
  0% {
    @include transform(scale(.9)); }
  10% {
    @include transform(scale(.9)); }
  35% {
    @include transform(scale(1.1)); }
  90% {
    @include transform(scale(.9)); }
  100% {
    @include transform(scale(.9)); } }
@include keyframes(preloader-die) {
  0% {
    @include transform(scale(1)); }
  100% {
    @include transform(scale(0)); } }
